@mixin fontFace($family,$weight) {
	@font-face {
		font-family: $family;
		src: url('fonts/#{$family}/Norm/#{$family}-#{$weight}.woff') format('woff'), url('fonts/#{$family}-#{$weight}.ttf') format('truetype');
		font-style: normal;
		font-weight: $weight; }

	@font-face {
		font-family: $family;
		src: url('fonts/#{$family}/Ital/#{$family}-#{$weight}-It.woff') format('woff'), url('fonts/#{$family}-#{$weight}-It.ttf') format('truetype');
		font-style: italic;
		font-weight: $weight; } }

@mixin border($color,$size: 1px,$borders: top left right bottom) {
	@each $border in $borders {
		border-#{$border}: #{$size} solid #{$color}; } }

@mixin MQ($breakpoint) {
	@if $breakpoint == 1700px {
		@media only screen and (min-width: #{$breakpoint}) {
			@content; } }
	@else {
		@media only screen and (max-width: #{$breakpoint}) {
			@content; } } }


@mixin nomp {
	margin: 0px;
	padding: 0px; }

@mixin borderbtm {
	height: 5px;
	background: url(i/sttv-border.png) no-repeat;
	background-size: 100% 5px; }

@mixin centerpos {
	top: 50%;
	left: 50%;
	transform: translate3d(-50%,-50%, 0); }

@mixin widebtn {
	@include wfull;
	padding: $small;
	margin-bottom: $tiny; }

@mixin btncolor {
	background: $azure;
	color: white; }

@mixin wfull {
	width: 100%; }

@mixin w3quarter {
	width: 75%; }

@mixin whalf {
	width: 50%; }

@mixin w1quarter {
	width: 25%; }
