/* CATEGORY STYLES */

.category-post {
	@include whalf;
	float: left;

	figure {
		@include wfull;
		display: block;

		img {
			display: block;
			@include wfull;
			height: auto; } }

	h5 {
		text-align: center; }

	figure, h5 {
		margin: 0;
		margin-bottom: 1em; } }

/** Mobile **/
@include MQ($mq-tablet) {
	.category-post {
		@include wfull;
		float: none; } }
