#account-block {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100vw;
	height: 100vh;
	visibility: hidden;
	background: $transwht;
	z-index: 999;
	opacity: 0;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;

	.menu-account-menu-container {
		max-width: $bodyw;
		width: 80%;
		padding: 1em;
		height: auto;
		position: absolute;
		top: 50%;
		left: 50%;
		background: $bodybg;
		transform: translate3d(-50%,-50%, 0); } }

.sttvmodal {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100vw;
	height: 100vh;
	visibility: hidden;
	z-index: 998;
	opacity: 0;
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out; }

body.login-sidebar-open #account-block, body.login-sidebar-open .sttvmodal {
	opacity: 1;
	visibility: visible; }
