#sidebar {
	display: block;
	@include w1quarter;
	min-height: $huge * 10;
	padding: 1em;

	>div {
		@include wfull;
		background-color: white;
		padding: 1em;

		#recentposts {
			font-size: $small;

			.collection-item {
				min-height: 4em;
				line-height: 1em; } }

		#subscribeform {
			position: relative;

			#subscribe-c2a {
				padding: 1em;
				font-size: $small; }

			h4 {
				margin: 0px; }

			a.button {
				@extend button;
				@include widebtn;
				display: block;
				text-align: center;

				h3 {
					text-transform: uppercase; } } } } }



/** Mobile **/
@include MQ($mq-tablet) {
	#sidebar {
		@include wfull; } }
